// Generated by CoffeeScript 2.3.2
var HasExampleModal, MainSection, Root, RootSection, copyrightText, pageTitle, title,
  boundMethodCheck = function(instance, Constructor) { if (!(instance instanceof Constructor)) { throw new Error('Bound instance method accessed before binding'); } };

import React, {
  Component,
  Fragment
} from "react";

import "./Root.css";

import {
  List,
  Title,
  ExternalLink
} from "./common";

import HeaderLinks from "./HeaderLinks";

import {
  ModalsWrapper,
  ExampleModal
} from "./Modals";

import Stuff from "./Stuff";

pageTitle = "BetterDiscordStuff";

copyrightText = "©2017-2019 square";

if (title = document.head.querySelector("title")) {
  title.innerText = pageTitle;
} else {
  title = document.createElement("title");
  title.innerText = pageTitle;
  document.head.appendChild(title);
}

export default Root = class Root extends Component {
  render() {
    return <Fragment>
      <header>
        <h1>{pageTitle}</h1>
        <HeaderLinks />
      </header>
      <main>
        <MainSection title="Stuff">
          <Stuff />
        </MainSection>
        <MainSection title="Contribute">
          <p><s>Hate the look of this site? Improve the CSS and show me. I will happily update it.</s><br />
            Thank you, <ExternalLink label="Metalloriff" url="https://github.com/Metalloriff/BetterDiscordPlugins" />!
            looks much better now. ;P</p>
        </MainSection>
      </main>
      <footer>
        <h3>{copyrightText}</h3>
      </footer>
      <ModalsWrapper />
    </Fragment>;
  }

};

RootSection = function({
    type: Tag,
    children
  }) {
  var inner;
  inner = <div className="root-section">
      {children}
    </div>;
  if (Tag) {
    return <Tag>{inner}</Tag>;
  } else {
    return inner;
  }
};

MainSection = function({title, children, list}) {
  if (list) {
    children = <List>{children}</List>;
  }
  return <Fragment>
    <Title label={title} />
    {children}
  </Fragment>;
};

HasExampleModal = class HasExampleModal extends Component {
  constructor() {
    super(...arguments);
    this.handleOnClick = this.handleOnClick.bind(this);
  }

  handleOnClick() {
    boundMethodCheck(this, HasExampleModal);
    return this.modal.open();
  }

  render() {
    return <div>
      <button type="button" onClick={this.handleOnClick}>Button</button>
      <ExampleModal ref={(modal) => {
        this.modal = modal;
      }} example={this.props.value} spawnPoint={this} />
    </div>;
  }

};



