  shortdesc: "If the autocompletion menu selects entries without your doing then this is for you.",
  longdesc: "When you type an `@` in the chat input, a list with possible mention targets comes up. In my case, if I happened to have the mouse cursor where that list pops up, it would auto-select the entry below it. This is supposed to only happen when you move the mouse cursor.\n\nIf you are having that same issue, then please read along for an explanation.\n\nHaving a `.net` application running can cause a systemwide spam of mousemove events.\nThis is a bug in .net itself and should ideally be fixed there. The next best option would be to treat it inside chromium's (discord's) message loop.\nSince neither of these 2 options are implemented I made this plugin to work around the ripples of Microsoft's buggy .net internals.",
