// sort list alphabetically
stuff[Symbol.iterator] = function*() {
  var i, len, results, sorted, x;
  sorted = Object.keys(this).sort();
  results = [];
  for (i = 0, len = sorted.length; i < len; i++) {
    x = sorted[i];
    results.push((yield this[x]));
  }
  return results;
};



